import './style.css'
import React, { useEffect, useState } from 'react'
import '../../components/app.css'
import { useHistory } from 'react-router-dom'
import * as logofull from '../../assets/images/favicon.png'
import * as logo from '../../assets/images/favicon.png'
import * as trash from '../../assets/trash.svg'
import * as send from '../../assets/send.png'
import * as edit from '../../assets/edit.png'

import { getAppdata, postAppdata } from 'utils/actions'
import Popup from 'components/Common/Popup'
import LoadingScreen from 'components/LoadingScreen'

const Dashboardscreen = () => {

  const history = useHistory()
  const [dashboard, setdashboard] = useState('')
  const [menu_open, setMenu_open] = useState(true)
  const [popup, setpopup] = useState(false)
  const [popup1, setpopup1] = useState(false)

  const [listItems, setlistItems] = useState([
    {
      "level": "",
      "title": "",
      "description": "",
      "loction": "",
      "image": "",
    }
  ])

  const [editdata, seteditdata] = useState({});

  const [level, setlevel] = useState('');
  const [title, settitle] = useState('');
  const [description, setdescription] = useState('');
  const [loction, setloction] = useState('');
  const [image, setimage] = useState('');

  const menu_items = [
    { path: 'Instance', icon: '', title: 'Instance' },
    { path: 'Logo', icon: '', title: 'Logo' },
    { path: 'Levels', icon: '', title: 'Levels' },
    { path: 'Sublevels', icon: '', title: 'Sublevels' },
    { path: 'Usersdata', icon: '', title: 'Usersdata' },
    { path: 'Analytics', icon: '', title: 'Analytics' },
  ]

  useEffect(() => {
    let token = JSON.parse(String(localStorage.getItem("AuthToken")))
    getAppdata(
      async (data: any, errorresponse: any) => {
        if (data.status === 200) {
          console.log("LIST :", data.data.result);
          setlistItems(data.data.result)
        } else {
          console.log('error ' + JSON.stringify(data))
          console.log('error ' + JSON.stringify(errorresponse))
        }
      }, token)

  }, [])



  const editrow = (element: any, ind: any) => {

    console.log(">>", listItems, ">>", element, ">>", ind)
    listItems.map((obj, index) => {
      console.log(">>", obj, ">>", index)

      if (index === ind) {
        seteditdata(obj)

        setlevel(obj.level)
        settitle(obj.title)
        setdescription(obj.description)
        setloction(obj.loction)
        setimage(obj.image)
      }
    })

  }


  const removerow = (element: any, index: any) => {

    console.log(listItems, element, index)
    let x = listItems
    let a = []
    for (let i = 0; i < x.length; i++) {
      if (i !== index)
        a.push(x[i])
    }
    setlistItems(a);
  }



  const renderHeader1 = () => {
    let headerElement = ['level', 'title', 'description', 'loction', 'logo']

    return headerElement.map((key, index) => {
      return <th key={index}>{key.toUpperCase()}</th>
    })
  }

  const renderBody1 = (element: any, index: any) => {
    // console.log("element=====", element);
    return (
      <tr>
        <td>{element.level}</td>
        <td>{element.title}</td>
        <td>{element.description}</td>
        <td>{element.loction}</td>
        <td><img className="dashboard_list_logo" src={element.image} /></td>
        <div style={{
          display: "flex",
        }}>
          <div className='table_edit_delete'>
            {popup ? null :
              <>
                <img onClick={() => {
                  setpopup(true)
                  editrow(element, index)
                  // console.log("teststuff",)
                }}
                  className='sendicon' src={edit} />
              </>
            }
          </div>

          <div className='table_edit_delete'>
            <>
              <img onClick={() => {
                setpopup1(true)
                removerow(element, index)
              }}
                className='sendicon' src={trash} />
            </>
          </div>
        </div>

      </tr >
    )
  }




  const renderHeader2 = () => {
    let headerElement = ['Vidideo', 'type', 'title', 'DIDYOUKNOW-type', 'DIDYOUKNOW', 'DIDYOUKNOW-Options', 'type', 'title', 'Activity-Type', 'Activity', 'Activity-Options', 'type', 'title', 'Quiz-Type', 'Quiz', 'Quiz-Options']

    return headerElement.map((key, index) => {
      return <th key={index}>{key.toUpperCase()}</th>
    })
  }

  const renderBody2 = (element: any, index: any) => {
    // console.log("element=====", element);
    let link = "https://www.youtube.com/embed/" + String(element.video)
    return (
      <tr>
        <td>
          <iframe
            width="420"
            height="120"
            src={link}
            frameBorder="0"
            allowFullScreen
            className="player"
          >
          </iframe>
        </td>

        {
          element.gamedata?.map((obj: any) =>
            <><td>{obj.types}</td><td>{obj.title}</td><td>{obj.bodytype}</td><td>{obj.body}</td><td>{obj.bodyoption}</td></>
          )
        }
      </tr >
    )
  }




  const renderHeader3 = () => {
    let headerElement = ['id', 'icon', 'mascot', 'title', 'sub title', 'body', 'bodytype', 'location', 'media']

    return headerElement.map((key, index) => {
      return <th key={index}>{key.toUpperCase()}</th>
    })
  }

  const renderBody3 = (element: any, index: any) => {
    // console.log("element=====", element);
    let link = "https://www.youtube.com/embed/" + String(element.video)
    return (
      <tr>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
      </tr >
    )
  }



  const renderHeader4 = () => {
    let headerElement = ['id', 'instance_id', 'l_logo', 'position', 'created_at', 'updated_at']

    return headerElement.map((key, index) => {
      return <th key={index}>{key.toUpperCase()}</th>
    })
  }

  const renderBody4 = (element: any, index: any) => {
    // console.log("element=====", element);
    let link = "https://www.youtube.com/embed/" + String(element.video)
    return (
      <tr>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
        <td>{"data data"}</td>
      </tr >
    )
  }



  const renderHeader = () => {
    let headerElement = ['level', 'title', 'logo']

    return headerElement.map((key, index) => {
      return <th key={index}>{key.toUpperCase()}</th>
    })
  }

  const renderBody = (element: any, index: any) => {
    // console.log("element=====", element);
    return (
      <tr>
        <td>{element.level}</td>
        <td>{element.title}</td>
        <td><img className="dashboard_list_logo" src={element.image} /></td>
        <div className='table_edit_delete'>
          {popup ? null :
            <>
              <img onClick={() => {
                setpopup(true)
              }}
                className='sendicon' src={edit} />
            </>
          }
          {popup &&
            <Popup
              title={"Edit Logo"}
              popup_body={
                <>
                  <input type="text" id="input_imagetextbox" className="input_imagetextbox" name="imageeditor" value={element.image}
                    onChange={(e) => {
                      // console.log(e.target.value);
                    }} />
                </>
              }
              confirmClick={() => {
                let token = JSON.parse(String(localStorage.getItem("AuthToken")))
                let Image = String(document.getElementById("input_imagetextbox").value);
                // console.log("Tadatada", Image);
                let temp = {
                  "image": Image,
                }

                // editrow(element, index)
                setimage('')

                // postAppdata(async (data: any, errorresponse: any) => {

                // }, token, data)

              }}
              cancelClick={() => {
                setpopup(false)
              }}
            />
          }
        </div>
      </tr >
    )
  }

  return (
    <div className='main_wrapper'>
      <div className='main_menu_left'>
        <div className='menu_logo_wrapper'>
          <div
            onClick={() => {
              history.replace('/')
            }}
          >
            <img className='main_menu_logo' src={menu_open ? logofull : logo} />
          </div>
        </div>
        <div className='menu_items_wrapper'>
          {menu_items.map((data: any) => (
            <div
              onClick={() => {
                setdashboard(data.path)
              }}
            >
              <img className='main_menu_item_icon' src={data.icon} />
              {menu_open ? (
                <div className='main_menu_item_title'>{data.title}</div>
              ) : null}
            </div>
          ))}
        </div>
      </div>

      {popup &&
        <Popup
          title={"Edit Logo"}
          popup_body={
            <>
              <input type="text" id="input_leveltextbox" className="input_imagetextbox" name="leveleditor" value={level}
                onChange={(e) => { setlevel(e.target.value) }} />
              <input type="text" id="input_titletextbox" className="input_imagetextbox" name="ititleeditor" value={title}
                onChange={(e) => { settitle(e.target.value) }} />
              <input type="text" id="input_descriptiontextbox" className="input_imagetextbox" name="descriptioneditor" value={description}
                onChange={(e) => { setdescription(e.target.value) }} />
              <input type="text" id="input_loctiontextbox" className="input_imagetextbox" name="loctioneditor" value={loction}
                onChange={(e) => { setloction(e.target.value) }} />
              <input type="text" id="input_imagetextbox" className="input_imagetextbox" name="imageeditor" value={image}
                onChange={(e) => { setimage(e.target.value) }} />
            </>
          }

          confirmClick={() => {
            setlistItems(listItems.map((obj) => {
              console.log("object===", obj.id, editdata.id);

              const getupdatedobj = () => {
                let a = obj
                a.level = String(document.getElementById("input_leveltextbox").value);
                a.title = String(document.getElementById("input_titletextbox").value);
                a.description = String(document.getElementById("input_descriptiontextbox").value);
                a.loction = String(document.getElementById("input_loctiontextbox").value);
                a.image = String(document.getElementById("input_imagetextbox").value);
                return a;
              }
              return obj.id == editdata.id ? getupdatedobj() : obj;
            }))

            setpopup(false)
          }}
          cancelClick={() => {
            setpopup(false)
          }}
        />
      }

      <div
        className={
          menu_open ?
            'page_wrapper page_shift_left' : 'page_wrapper margin_left'
        }
      >

        {dashboard === "Instance" &&
          <div>
            <h1>Instance</h1>

            <div>
              <label for="Instance">Choose a Instance</label>

              <select name="Instance" id="Instance">
                <option value="A">A</option>
                <option value="B">B</option>
                <option value="C">C</option>
                <option value="D">D</option>
              </select>
            </div>

            <div className="internal_table">
              <table id='internal_table'>
                <thead>
                  <tr>{renderHeader3()}</tr>
                </thead>
                <tbody>{listItems.map(renderBody3)}</tbody>
              </table>
            </div>
          </div>
        }
        {dashboard === "Logo" &&
          <div className="pageloadmainparnet">
            <h1>LOGO</h1>

            <div>
              <label for="Instance">Choose a Instance</label>

              <select name="Instance" id="Instance">
                <option value="A">A</option>
                <option value="B">B</option>
                <option value="C">C</option>
                <option value="D">D</option>
              </select>
            </div>

            <div className="internal_table">
              <table id='internal_table'>
                <thead>
                  <tr>{renderHeader4()}</tr>
                </thead>
                <tbody>{listItems.map(renderBody4)}</tbody>
              </table>
            </div>

            <div className="pageloadmain">
              <LoadingScreen />
            </div>
          </div>
        }

        {dashboard === "Levels" &&
          <>
            <div>
              <h1>Levels</h1>

              <div>
                <label for="Instance">Choose a Instance</label>

                <select name="Instance" id="Instance">
                  <option value="A">A</option>
                  <option value="B">B</option>
                  <option value="C">C</option>
                  <option value="D">D</option>
                </select>
              </div>


              <div className="internal_table">
                <table id='internal_table'>
                  <thead>
                    <tr>{renderHeader1()}</tr>
                  </thead>
                  <tbody>{listItems.map(renderBody1)}</tbody>
                </table>
              </div>
            </div>
          </>
        }

        {dashboard === "Sublevels" &&
          <div>
            <h1>Sublevels</h1>

            <div>
              <label for="Instance">Choose a Instance</label>

              <select name="Instance" id="Instance">
                <option value="A">A</option>
                <option value="B">B</option>
                <option value="C">C</option>
                <option value="D">D</option>
              </select>
            </div>

            <div className="internal_table">
              <h6>Level1</h6>
              <table id='internal_table'>
                <thead>
                  <tr>{renderHeader2()}</tr>
                </thead>
                <tbody>{listItems.map(renderBody2)}</tbody>
              </table>
            </div>

          </div>
        }

        {dashboard === "Usersdata" &&
          <>
            <div>
              <h1>Usersdata</h1>
              <div className="internal_table">
                <table id='internal_table'>
                  <thead>
                    <tr>{renderHeader()}</tr>
                  </thead>
                  <tbody>{listItems.map(renderBody)}</tbody>
                </table>
              </div>
            </div>
          </>
        }


        {dashboard === "Analytics" &&
          <>
            <div>
              <h1>Analytics</h1>
              <div className="internal_table">
                <table id='internal_table'>
                  <thead>
                    <tr>{renderHeader()}</tr>
                  </thead>
                  <tbody>{listItems.map(renderBody)}</tbody>
                </table>
              </div>
            </div>
          </>
        }



      </div>


    </div>
  )
}

export default Dashboardscreen
